*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  outline: none;
}

html {
  height: 100%;
}

body {
  line-height: 1;
  font-family: "Manrope", sans-serif;
  position: relative;
  font-family: "Manrope", sans-serif;
  color: rgb(32, 33, 37);
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

strong {
  font-weight: bold;
}

input {
  outline: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  border: 0;
  max-width: 100%;
}

a {
  display: inline-block;
  text-decoration: none;
  cursor: pointer;
}

.btn, .header__cta .btn {
  padding: 12px 24px;
  border-radius: 50px;
  background-color: rgb(25, 103, 210);
  color: #fff;
  text-decoration: none;
  text-align: center;
  transition: background-color 0.3s;
}
.btn:hover {
  background-color: #1451a4;
}

.header {
  background-color: #fff;
  box-shadow: 0px 2px 6px 0px rgba(32, 33, 37, 0.1);
  z-index: 99;
  width: 100%;
  top: 0;
  z-index: 1000;
  transition: all 0.3s ease;
}
.header.sticky {
  position: fixed;
  top: -100px;
  width: 100%;
  transition: top 0.3s ease;
}
.header.sticky.show {
  top: 0;
}
.header__wrapper {
  width: 328px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  justify-content: start;
  padding: 8px 0;
}
@media (min-width: 360px) {
  .header__wrapper {
    width: 328px;
    margin: 0 auto;
  }
}
@media screen and (min-width: 768px) {
  .header__wrapper {
    width: 680px;
    margin: 0 auto;
  }
}
@media screen and (min-width: 1300px) {
  .header__wrapper {
    width: 1269px;
    margin: 0 auto;
  }
}
.header__hamburger {
  display: block;
  margin-right: 16px;
}
.header__hamburger-button {
  background: none;
  border: none;
  cursor: pointer;
}
