  margin-right: 70px;
}
.header__logo svg {
  width: 118px;
}
.header__nav {
  display: none;
}
.header__nav-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}
.header__nav-li:last-child {
  margin-right: 0;
}
.header__nav-li-link {
  text-decoration: none;
  color: rgb(95, 99, 104);
  transition: color 0.3s;
  font-family: Manrope;
  font-size: 16px;
  font-weight: 500;
  line-height: 22px;
  padding: 9px 13px;
}
.header__nav-li-link:hover {
  color: rgb(25, 103, 210);
}
.header__nav-li-link.selected-link {
  font-weight: bold;
  color: rgb(25, 103, 210);
}
.header__cta {
  display: none;
  margin-left: auto;
}
.header__cta .btn {
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  padding: 14px 24px;
}
