.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
  display: none;
}
.modal.active {
  display: block;
}
.modal__header {
  padding: 12px 28px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 2px 6px 0px rgba(32, 33, 37, 0.1);
}
.modal__menu {
  position: absolute;
  top: 0;
  left: -100%;
